.footer{
display: none;
}

.f-icons-text{
    display: none;
}

@media screen and (max-width: 775px) {
    .footer{
        width: 100%;
        height: 12vh;
        background: rgb(219, 216, 216);
        position: fixed;
        bottom: 0rem;
        opacity: 80%;
        display: flex;
        justify-content: center;
    }

    .f-icons-text{
        color: black;
        font-size: 14px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        margin: 12px;
    }
    

}