.whatsApp{
    width: 50%;
    display: flex;
    position: relative;
    left: 5rem;
    justify-content:center;
    flex-direction: none;
    align-items:center;
    font-size: 11px;

}

a{
    text-decoration: none;
}

.whap-img{
    width: 3rem;
    padding: 5px;
    padding-left: 10px;
    margin-left: -2.5rem;
    margin-right: 10px;
}

.gogle-img{
    width: 4rem;
    margin-left: -5rem;
    padding: 0;
}

.img-hand{
    width: 2rem;
}
.hola h1{
    display: none;

}
.box-1{
    background:rgba(255, 255, 255, 0.85);
    border: 1px solid var(--primary-color);
    border-radius: 20px;
    display:flex;
    justify-content: center;
    align-items: center;
    width: 70%;
    margin: 3rem;
}

.box-1:hover {
    cursor: pointer;
    color: var(--white-color);
    background: var(--primary-color)
}

.box-2 {
    background:rgba(255, 255, 255, 0.85);
    display:flex;
    justify-content: center;
    align-items:center;
    width: 70%;
    border: 1px solid var(--primary-color);
    border-radius: 20px;
}

.box-2:hover {
    cursor: pointer;
    color: var(--white-color);
    background: var(--primary-color);
}

.insta {
    margin-top: 1rem;
}

.insta-img {
    width: 4rem;
    padding: 5px;
    padding-left: 10px;
    margin-left: -5.5rem;
    margin-right: 10px;
}

.service {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.s-encordado{
    width: 50%;
    margin-top: 2rem;
    display: flex;
    flex-direction: column;
    justify-content:center;
    align-items: center;
    
}

.titulo-mano{
    width: 10rem;
    height: 20px;
    left: 1rem;
    position: relative;
    padding-top: 4px;
    font-size: 13px;
    background:var(--primary-color);
    color: aliceblue;
    text-align: center;
    justify-content: center;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
}
.s-mano{
    width: 10rem;
    background: rgba(255, 255, 255, 0.85);
    padding-top: 0;
    padding: 0.5rem;
    left: 1rem;
    display: flex;
    position: relative;
    justify-content: center;
    align-items: flex-start;
    flex-direction:column;
    border: 1px solid var(--secundary-color);
    border-top: 0;
    border-radius: 20px;
    font-size: 13px;
    border-top-left-radius: 0px;
    border-top-right-radius: 0px;
}


    @media screen and (max-width: 1100px){
        .s-mano{
            position: relative;
            left: 3rem;
        }
    }
    .whatsApp{
        display: flex;
        flex-direction: column;
        position: relative;
        left: -2rem;
        font-size: 11px;
    }
    .box-1{
        width: 50%;
        margin: 1rem;
    }

    .box-2{
        width: 50%
    }

    @media screen and (max-width: 812px){
        .s-mano{
            left: 1rem;
            width: 10rem;
        }
    }

    @media screen and (max-width: 720px){

        .hola h1{
            width: 100%;
            top: -4.5rem;
            position: relative;
            left: 56px;
            display: flex;
        }

        .s-encordado{
            margin-top: 3.5rem;
            left: 1rem;
        }

        .titulo-mano{
            left: -2.9rem;
        }
        .s-mano{
            position: relative;
            width: 10rem;
            left: -2.9rem;
        }
        
        .whatsApp{
            top: 0.4rem;
            left: -2.5rem;
            font-size: 11px;
        }
        .box-1{
            width: 10rem;
            margin-top: 1rem;
        }

        .gogle-img{
            margin-left: -2rem;
        }
    
        .box-2{
            width: 83%;
            font-size: 10px;
        }

        .whap-img{
            margin-left: 0rem;
        }

        .insta-img {
            margin-right: 0rem;
            margin-left: -3.5rem;
        }

    }