.card {
    background: white;
    width: 9rem;
    height: 10rem;
    margin: 18px;
    display: flex;
    position: relative;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border: 1px solid var(--secundary-color);
    border-radius:20px;
}

.c-img {
    width: 5rem;
}
.c-name {
    font-size: 13px;
    font-weight: bold;
    position: relative;
}

.c-precio {
    display: flex;
    justify-content: center;
    align-items: center;
    width:100%;
    height: 25px;
    position: relative;
    top: 22px;
    background: var(--secundary-color);
    color: white;
    border-bottom-left-radius: 20px;
    border-bottom-right-radius: 20px;
}

@media screen and (max-width: 600px){
    .card {
        width: 6.5rem;
        height:8rem;
        margin: 20px;
    }

    .c-img {
        width: 65px;
        height: 75px;
    }

    .c-name {
        font-size: 10px;
        text-align: center;
        bottom: -7px;
    }
    
    .c-precio {
        top: 9px;
        width: 101%;
    }
}