.cuerdas{
  background: rgba(255, 255, 255, 0.85);
  width: 60%;
  height: auto;
  padding: 1rem;
  margin: 3rem;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  border: 1px solid gray;
  border-radius: 20px;
  border-top-left-radius: 0;
}

.nota {
  display: block;
  width: 100%;
  font-size: 18px;
  color: var(--secundary-color);
  text-align: center;
}

.cuerdas::before {
  content: "Encordados";
  position: absolute;
  top: -30px;
  left: -1px;
  font-size: 25px;
  color: aliceblue;
  background: var(--primary-color);
  width: 10rem;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
  text-align: center;

}

@media screen and (max-width: 600px) {
  .cuerdas::before{
      width: 100.5%;
  }

  .nota{
    font-size: 13px;
  }

  .cuerdas {
      border-top-right-radius: 0;
      width: 90%;
      padding: 0.5rem;
      flex-direction: row;
      
  }
}