.loading {
    height: 100vh;
    position: relative;
    background: var(--primary-color);
    animation: show 10000ms linear forwards;
}

@keyframes show {
    0% { opacity: 1; }
    50% { opacity: 0.5; }
    100% { opacity: 0; }
}

.l-div{
    margin: 0px;
    position: absolute;
    top: 50%;
    left: 50%;
    -ms-transform: translate( -50%, -50%);
    transform: translate( -50%, -50%);
}

.img-logo50{
    width: 250px !important;
    height: 250px !important;
    border-radius: 100%;
}