.accesorios{
    background: rgba(255, 255, 255, 0.85);
    width: 60%;
    height: auto;
    padding: 1rem;
    margin: 3rem;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    border: 1px solid gray;
    border-radius: 20px;
    border-top-left-radius: 0;
}

.accesorios::before {
    content: "Accesorios";
    position:absolute;
    top: -30px;
    left: -1px;
    font-size: 25px;
    color: aliceblue;
    background: var(--primary-color);
    width: 10rem;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
    text-align: center;

}

@media screen and (max-width: 600px) {
    .accesorios::before{
        width: 100.5%;
    }

    .accesorios {
        border-top-right-radius: 0;
        width: 90%;
        padding: 0.5rem;
        flex-direction: row;
        margin-bottom: 7rem;
    }
}