.navbar{
    height: 11vh;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: var(--primary-color);
    color: var(--white-color);
    padding-right: 0.5rem;
}

.n-left{
    align-items: center;
    display: flex;
    margin: 10px;
    padding: 1rem;
}

.n-logo{
    width: 7rem;
    background: var(--white);
    border-radius: 100%;
    padding: 5px;
    margin-right: 1rem;
}

.n-name {
    font-size: 1.3rem;
    font-weight: bold;
}

.n-right{
    display: flex;
    flex: 1;
    align-items: center;
    justify-content: center;
    font-weight: 400;
}

.n-list{
    width: 60%;
}

.n-list>ul{
    display: flex;
    cursor: pointer;
}

.n-list>ul>li{
    margin: 1rem;
}

.n-li {
    margin: 1rem;
    padding: 10px;
}

.n-li:hover {
    background: #4ea2d7;
    border-radius: 20px;
}

.n-list>ul>li:hover{
    cursor: pointer;
}

.n-btn{
    width: 8rem;
    height: 3rem;
    font-size: 20px;
    font-weight: bold;
    border-radius: 2rem;
    background-color: var(--primary-color);
    border: 3px solid white;
    color: var(--white-color);
    cursor: pointer;
}

.n-btn:hover {
    color: var(--primary-color);
    font-weight: bold;
    background: var(--white-color);
}

@media screen and (max-width: 926px){

    .n-right {
        justify-content: start;
        font-weight: bold;
        font-size: 14px;
    }

    }

@media screen and (max-width: 720px) {
    .navbar{
        width: 100%;
        padding: 0;
        justify-content: start;
    }

    .n-logo{
        margin-right: 5rem;
        width: 10rem;

    }

    .n-left{
        width: 100%;
        margin: 0;
        padding: 0;
        transform: scale(0.7);
        position: relative;
        left: -50px;
    }

    .n-name{
        width: 10rem;
        position:relative;
        right: -8rem;
        
    }

    .n-right{
        display: none;
    }

    .n-list{
        display: none;
    }

    .n-btn{
        display: none;
    }
}